module.exports = "/* ****************** */\n/* Progression Circle */\n/* ****************** */\n.playlist-stats {\n  display: -ms-grid;\n  display: grid;\n  justify-content: flex-start;\n  align-items: center;\n  -ms-grid-columns: (1fr)[3];\n      grid-template-columns: repeat(3, 1fr);\n  grid-column-gap: 20px; }\n.playlist-stats .stat {\n    text-align: center;\n    border: 1px solid #757575;\n    padding: 1.875em 1.25em;\n    border-radius: 5px; }\n.playlist-stats .stat span, .playlist-stats .stat p {\n      display: block;\n      line-height: 1; }\n.playlist-stats .stat span {\n      font-size: 0.875em; }\n"